import { _decorator, Component, Node } from 'cc';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { UIAnnouncement } from 'db://assets/module_basic/ui_announcement/UIAnnouncement';
import { SceneDef } from 'db://assets/scripts/SceneDef';
const { ccclass, property } = _decorator;

@ccclass('SlotsUnicornLobbyScene')
export class SlotsUnicornLobbyScene extends Component {
    @property(Node) announcementPlacer: Node;

    async start() {
        if (!UserMgr.inst.uid) {
            tgx.SceneUtil.loadScene(SceneDef.START);
            return;
        }

        tgx.UIMgr.inst.closeAll();

        // if (lobbyNet.type != 'http') {
        //     tgx.UIMgr.inst.showUI(UIChat, null, null, lobbyNet);
        // }

        tgx.UIMgr.inst.showUI(UIAnnouncement, (ui: UIAnnouncement) => {
            ui.setPosition(this.announcementPlacer.position.x, this.announcementPlacer.position.y);
        });
    }


    update(deltaTime: number) {

    }

    async onBtnSoloModeClicked() {
        // SlotKylinGameMgr.inst.initSoloMode();
        SubGameMgr.inst.CreateRoom(SubGameDef.SLOTS_UNICORN, true);
    }
}


