import { Enum } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

export interface SlotTigerStartGameReq {
    betScore: number,
    winScore: number,
    code: number,
    elements: SlotTigerElement[],
    lines: SlotTigerLine[],
    mini: SlotTigerMini,
    isAllSame: number
}

export interface SlotTigerElement {
    elementIds: number[]
}

export interface SlotTigerMini {
    miniElementId: number,
    miniElements: SlotTigerElement[],
    miniLines: SlotTigerLine[],
    miniWinScore: number
}

export interface SlotTigerLine {
    line: number[],
    lineNo: number,
    lineScore: number
}

export namespace SlotUnicornData {
    /**
     * 游戏状态
     */
    export enum GameStatus {
        start, //游戏开始
        over,//游戏结束
    }
    Enum(GameStatus);

    const MessgeDefine = {
        Request: {
            start: "startgame",
            selected: "selected",
        },
        Response: {
            GameScene: "slot_tiger_game_scene",
        }
    }

    const EventName = {
        "clickGameItem": "clickGameItem",
    }

    const Auto = {
        AutoIndex: [10, 30, 50, 100, 1000],
    }

    const cacheName = {
        betScore: '_betScore'
    }

    /**
     * 游戏静态常量
     */
    @ccclass('GameConst')
    export class GameConst extends Component {
        public static MessgeDefine = MessgeDefine;
        public static EventName = EventName;
        static cacheName = cacheName;

        public static isEntryMini: boolean = false;
        public static Auto = Auto;
        public static autoCountIndex = 0;
        public static leftAutoCount = 0;
        public static reduceLabel = 0;
        public static addLabel = 0;
        public static winLabel = 0;
    }
}


