import { Tween } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { Vec3, tween, easing } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UnicornGameItem } from './UnicornGameItem';
import { SlotUnicorn } from './GameManager';
import { randomRangeInt } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('SlotUnicornScorll')
export class SlotUnicornScorll extends Component {
    @property({ type: Prefab, displayName: "滚动的对象" })
    item: Prefab = null;

    private _startTw: Tween<Node>[] = [];

    public addIteem() {
        for (let i = 1; i <= 3; i++) {
            const item = instantiate(this.item);
            item.parent = this.node;
            item.name = (i - 1).toString();
            item.position = new Vec3(0, 638 + i * 319);
            SlotUnicorn.GameManager.instance.setImage(item);
        }
    }

    /**
     * 滚动，每一列的
     * @param repeat 循环次数
     * @param maxPos 最高位置
     * @param distance 单次移动距离(间隔距离)
     */
    public startScroll(maxPos: Vec3 = new Vec3(0, 638), distance: number = 319) {
        //加一个新item在最上面
        const element = instantiate(this.item);
        element.parent = this.node;
        element.setSiblingIndex(-1);
        element.position = maxPos;
        SlotUnicorn.GameManager.instance.setImage(element);

        for (let i = 0; i < this.node.children.length; i++) {
            const pos = this.node.children[i].position;
            const pos1 = new Vec3();
            Vec3.add(pos1, pos, new Vec3(0, -distance));
            tween(this.node.children[i]).removeSelf();
            tween(this.node.children[i]).to(.5 * SlotUnicorn.GameManager.instance.speed, { position: pos1 }, { easing: easing.backIn }).
                call((node: Node) => {
                    tween(node).removeSelf();
                    if (node.position.y < -600) {
                        node.position = maxPos;
                        node.setSiblingIndex(-1);
                        SlotUnicorn.GameManager.instance.setImage(node);

                    }

                    const tw = tween(node).by(.05 * SlotUnicorn.GameManager.instance.speed, { position: new Vec3(0, -distance) })
                        .call((node: Node) => {
                            tween(node).removeSelf();
                            if (node.position.y < -600) {
                                node.position = maxPos;
                                node.setSiblingIndex(-1);
                                SlotUnicorn.GameManager.instance.setImage(node);
                            }
                        })
                        .union()
                        .repeatForever()
                        .tag(1200);
                    this._startTw.push(tw);
                    tw.start();
                })
                .start();
        }
    }

    /**
     * 开始结束滚动
     * @param faceIds 元素id集合 
     * @param isMini 是否小游戏
     */
    public endScroll(faceIds: number[], isMini: boolean, repeat, callback: Function = null) {
        const maxPos: Vec3 = new Vec3(0, 638); //普通游戏时的最高点
        const distance: number = 319; //每个元素距离
        for (let i = 0; i < this._startTw.length; i++) {
            this._startTw[i] && this._startTw[i].stop();
            this.node.children[i] && (this.node.children[i].position = new Vec3(0, maxPos.y - i * distance));
        }
        this._startTw = [];
        for (let i = 0; i < this.node.children.length; i++) {
            const pos = this.node.children[i].position;
            const pos1 = new Vec3();
            Vec3.add(pos1, pos, new Vec3(0, -distance));
            tween(this.node.children[i]).removeSelf();
            tween(this.node.children[i]).to(.05 * SlotUnicorn.GameManager.instance.speed, { position: pos1 }).
                call((node: Node) => {
                    tween(node).removeSelf();
                    if (node.position.y < -600) {
                        if (!isMini) {
                            node.destroy();
                        } else {
                            node.position = new Vec3(0, 638);
                            node.setSiblingIndex(-1);
                        }
                    }

                    if (i === this.node.children.length - 2) {
                        this.setEndFace(faceIds, repeat, isMini, callback);
                    }
                })
                .tag(1200)
                .start();
        }
    }

    //最高固定的牌面，顺滑变面
    private setEndFace(faceIds: number[], repeats: number, isMini: boolean, callback: Function = null) {
        const distance: number = 319; //每个元素距离
        if (!isMini) {
            for (let i = 2; i >= 0; i--) {
                const item = instantiate(this.item);
                item.parent = this.node;
                item.setSiblingIndex(-1);
                item.position = new Vec3(0, distance * 2 + i * distance);
                if (isMini) SlotUnicorn.GameManager.instance.setImage(item, SlotUnicorn.GameManager.instance.miniElementId);
                else SlotUnicorn.GameManager.instance.setImage(item, faceIds[2 - i]);
            }
        } else {
            for (let i = 0; i < this.node.children.length; i++) {
                const ran = randomRangeInt(0, 3);
                this.node.children[i].active = true;
                SlotUnicorn.GameManager.instance.setImage(this.node.children[i], SlotUnicorn.GameManager.instance.miniElementId);
                this.node.children[i].position = new Vec3(0, distance * 2 + ran * distance);
                if (i >= 4) this.node.children[i].active = false;
            }
        }

        for (let i = 0; i < this.node.children.length; i++) {
            tween(this.node.children[i]).removeSelf();
            tween(this.node.children[i]).by(.05 * SlotUnicorn.GameManager.instance.speed, { position: new Vec3(0, -distance) }).
                call((node: Node) => {
                    tween(node).removeSelf();
                    if (node.position.y < -600) {
                        if (!isMini) {
                            node.destroy();
                        } else {
                            node.position = new Vec3(0, 638);
                            node.setSiblingIndex(-1);
                        }
                    }
                })
                .union()
                .repeat(repeats)
                .call((node: Node) => {
                    if (!isMini) {
                        this.scheduleOnce(() => {
                            AudioMgr.inst.playOneShot('audio/sfx_reel_stop', 1, ModuleDef.SLOTS_UNICORN);
                        }, 0.2 * SlotUnicorn.GameManager.instance.speed);
                        tween(node).by(.5 * SlotUnicorn.GameManager.instance.speed, { position: new Vec3(0, -distance) }, { easing: easing.backOut }).
                            call((node: Node) => {
                                tween(node).removeSelf();
                                if (node.position.y < -600) {
                                    node.destroy();
                                } else {
                                    node.getComponent(UnicornGameItem).addGift();
                                }
                            })
                            .tag(1200)
                            .start();
                    } else {
                        SlotUnicorn.GameManager.instance.setImage(node, SlotUnicorn.GameManager.instance.miniElementId);
                        tween(node).to(0.5 * SlotUnicorn.GameManager.instance.speed, { position: new Vec3(0, -1400) }, { easing: easing.backOut }).
                            call((node: Node) => {
                                tween(node).removeSelf();
                                if (node.position.y < -600) {
                                    node.position = new Vec3(0, 638);
                                    node.setSiblingIndex(-1);
                                    SlotUnicorn.GameManager.instance.setImage(node, SlotUnicorn.GameManager.instance.miniElementId);
                                }
                                callback && callback(Number(this.node.name));
                                for (let j = 0; j < this.node.children.length; j++) {
                                    this.node.children[j].position = new Vec3(0, 638 + i * distance);
                                }
                            })
                            .tag(1200)
                            .start();
                    }
                })
                .start();
        }
    }
}


