import { _decorator, Component, Node } from 'cc';
import { SlotTigerElement, SlotTigerLine } from '../data/GameConst';
import { SlotUnicorn } from './UIManager';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { find } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { UnicornGameItem } from './UnicornGameItem';
import * as SlotGame from './GameManager';
import { macro } from 'cc';
import { randomRangeInt } from 'cc';
import { UnicornUtils } from '../data/UnicornUtils';
import { tween } from 'cc';
import { easing } from 'cc';
import { UnicornAniManager } from './UI/UnicornAniManager';
import { Label } from 'cc';
import { Mask } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
const { ccclass, property } = _decorator;

/**
 * 中奖后的展示管理
 */
@ccclass('UnicornEndManager')
export class UnicornEndManager extends Component {
    @property(Node)
    blackNode: Node = null;
    @property(Node)
    lineContent: Node = null;
    @property(Node)
    turbleContent: Node = null;
    @property({ type: Prefab, displayName: "中奖对象" })
    rewardPrefab: Prefab = null;
    @property([SpriteFrame])
    lightFrames: SpriteFrame[] = [];
    @property(Node)
    winTip: Node = null;

    private static _instance: UnicornEndManager = null;
    public static get instance() {
        return this._instance;
    }

    private _leftLightContent: Node = null;
    private _rightLightContent: Node = null;
    private _unicornElements: SlotTigerElement[] = [];
    public set unicornElements(value) {
        this._unicornElements = value;
    }

    private _rewardLines: Node[][] = [];
    private _rewardLineNo: number[] = [];
    private _isElementLight: boolean[][] = [];
    private _playIndex = 0;
    public lightIndexList: number[][] = [];
    private _data: SlotTigerLine[] = [];
    public isRunMiniScroll: number = 0;

    protected onLoad() {
        if (UnicornEndManager._instance) {
            this.node.destroy();
            return;
        }

        UnicornEndManager._instance = this;
        this._leftLightContent = SlotUnicorn.UIManager.instance.leftLightContent;
        this._rightLightContent = SlotUnicorn.UIManager.instance.rightLightContent;

        for (let i = 1; i <= 5; i++) {
            this._rewardLines[i] = [];
        }

        for (let i = 0; i < 3; i++) {
            this._isElementLight[i] = [false, false, false];
            this.lightIndexList[i] = [];

            for (let j = 0; j < 3; j++) {
                const item = instantiate(this.rewardPrefab);
                item.parent = find(i.toString(), this.turbleContent);
                item.name = j.toString();
                item.position = new Vec3(0, 638);
                item.active = false;
            }
        }
    }

    protected onDestroy(): void {
        UnicornEndManager._instance = null;
    }


    public showTurnble(data: SlotTigerLine[]) {
        if (data.length <= 0) return;
        this._data = data;
        AudioMgr.inst.playOneShot('audio/sfx_multipiler', 1, ModuleDef.SLOTS_UNICORN);
        this.blackNode.active = true;
        for (let i = 0; i < data.length; i++) {
            const lineNo = data[i].lineNo.toString();
            const line = find(lineNo, this.lineContent);
            line.active = true;
            this._rewardLines[lineNo].push(line);
            this._rewardLineNo.push(data[i].lineNo);
            find(lineNo, this._leftLightContent).getComponent(Sprite).spriteFrame = this.lightFrames[1];
            find(lineNo, this._rightLightContent).getComponent(Sprite).spriteFrame = this.lightFrames[1];

            for (let j = 0; j < data[i].line.length; j++) {
                const parent = find(j.toString(), this.turbleContent);
                const faceIndex = data[i].line[j];
                const item = find(faceIndex.toString(), parent);
                item.active = true;
                item.position = new Vec3(0, 319 - faceIndex * 319);
                const gameItem: UnicornGameItem = item.getComponent(UnicornGameItem);
                gameItem.setNull();
                gameItem.setAnimation(this._unicornElements[j].elementIds[faceIndex], "2");
                gameItem.showEffect();
                this._rewardLines[lineNo].push(item);
            }
        }

        this.turbleContent.getComponent(Mask).enabled = false;
        this._rewardLineNo.sort((a, b) => {
            return a - b;
        });
        AudioMgr.inst.playOneShot('audio/sfx_win_line', 1, ModuleDef.SLOTS_UNICORN);
        UnicornAniManager.instance.playLeer();
        if (data.length > 1) {
            this.schedule(this.playReward, 1.5, macro.REPEAT_FOREVER);
        }
    }

    public setMiniElement() {
        if (this.isRunMiniScroll >= 1) return;
        this.isRunMiniScroll++;
        SlotUnicorn.UIManager.instance.stopShot();
        const indexList: number[][] = [];
        //初始数组
        for (let j = 0; j < 3; j++) {
            indexList[j] = [];
        }
        for (let i = 0; i < 3; i++) {
            //取未点亮的标志位
            let index = UnicornUtils.instance.getRandomNumberNotInArray(0, 2, this.lightIndexList[i]);
            if (index === -1) continue;
            indexList[i].push(index);
        }

        let num = 0;
        //计算是否每列都有值被取到
        for (let j = 0; j < 3; j++) {
            if (indexList[j].length > 0) {
                num++;
            }
        }

        if (num === 3) {
            //每列都有值被取到
            const iList = [];
            const ran = randomRangeInt(1, 3);
            //随机删除n个位置的值
            for (let i = 0; i < ran; i++) {
                const index = UnicornUtils.instance.getRandomNumberNotInArray(1, 2, iList);
                indexList[index] = [];
                iList.push(index);
            }
        }
        //每列都没有值被取到，已满
        if (num === 0) return;
        this.showMiniElement(indexList);
    }

    public showMiniElement(indexList: number[][]) {
        UnicornUtils.instance.startLoad((i: number) => {
            if (indexList[i].length === 0) return;
            const randomNum = indexList[i][0];
            if (randomNum != -1) {
                this.lightIndexList[i].push(randomNum);
                const parent = find(i.toString(), this.turbleContent);
                const item = find(randomNum.toString(), parent);
                item.active = true;
                item.getComponent(UnicornGameItem).closeEffect();
                item.position = new Vec3(0, 638 + (2 - randomNum) * 319);
                SlotGame.SlotUnicorn.GameManager.instance.setImage(item, this._unicornElements[i].elementIds[randomNum]);
                if (this._unicornElements[i].elementIds[randomNum] != 0) {
                    this._isElementLight[i][randomNum] = true;
                }
                item.scale = new Vec3(0.9, 0.9);
                tween(item).removeSelf();
                tween(item).to(.1, { position: new Vec3(0, 319 - randomNum * 319) }, { easing: easing.backOut })
                    .delay(0.1)
                    .call((cbNode: Node) => {
                        tween(cbNode).to(0.1, { scale: Vec3.ONE }).call(() => {
                            for (let i = 0; i < 3; i++) {
                                AudioMgr.inst.playOneShot('audio/sfx_reel_stop', 1, ModuleDef.SLOTS_UNICORN);
                            }

                            const lineNoList = SlotGame.SlotUnicorn.GameManager.instance.getAllLineNo(this._isElementLight, this._rewardLineNo);
                            for (let i = 0; i < lineNoList.length; i++) {
                                if (i === 0) AudioMgr.inst.playOneShot('audio/sfx_win_line', 1, ModuleDef.SLOTS_UNICORN);
                                const lineNo = lineNoList[i].toString();
                                const line = find(lineNo, this.lineContent);
                                line.active = true;
                                this._rewardLineNo.push(lineNoList[i]);
                                find(lineNo, this._leftLightContent).getComponent(Sprite).spriteFrame = this.lightFrames[1];
                                find(lineNo, this._rightLightContent).getComponent(Sprite).spriteFrame = this.lightFrames[1];
                                const lineIndexList = SlotGame.SlotUnicorn.GameManager.instance.getLineNo(lineNo);
                                for (let j = 0; j < this.turbleContent.children.length; j++) {
                                    const element = find(lineIndexList[j].toString(), this.turbleContent.children[j]);
                                    const gameItem: UnicornGameItem = element.getComponent(UnicornGameItem);
                                    gameItem.showEffect();
                                }
                            }
                            this.isRunMiniScroll = 0;
                        }).tag(1200).start();

                    }).tag(1200).start();
            }
        }, indexList.length);
    }

    private playReward() {
        AudioMgr.inst.playOneShot('audio/sfx_win_line', 1, ModuleDef.SLOTS_UNICORN);
        if (this._playIndex >= this._rewardLineNo.length) {
            this._playIndex = 0;
            this.winTip.active = false;
            //中奖线全部展示
            for (let i = 0; i < this._rewardLineNo.length; i++) {
                for (let j = 0; j < this._rewardLines[this._rewardLineNo[i]].length; j++) {
                    this._rewardLines[this._rewardLineNo[i]][j].active = true;
                    this.setLightFrame(i, 1);
                }
            }
        } else {
            for (let i = 0; i < this._rewardLineNo.length; i++) {
                for (let j = 0; j < this._rewardLines[this._rewardLineNo[i]].length; j++) {
                    this._rewardLines[this._rewardLineNo[i]][j].active = false;
                    this.setLightFrame(i, 0);
                }
            }
            this.winTip.active = true;
            if (this._rewardLineNo[this._playIndex] === 2) {
                this.winTip.position = new Vec3(0, 740);
            } else if (this._rewardLineNo[this._playIndex] === 3) {
                this.winTip.position = new Vec3(0, 100);
            } else {
                this.winTip.position = new Vec3(0, 420);
            }
            const winLabel = find("Label", this.winTip).getComponent(Label);
            winLabel.string = Num.exchangeToThousands(1, this.getLineScore(this._rewardLineNo[this._playIndex]));
            //展示其中的一条
            for (let i = 0; i < this._rewardLines[this._rewardLineNo[this._playIndex]].length; i++) {
                this._rewardLines[this._rewardLineNo[this._playIndex]][i].active = true;
                this.setLightFrame(this._playIndex, 1);
            }
            this._playIndex++;
        }
    }

    private getLineScore(no: number) {
        for (let i = 0; i < this._data.length; i++) {
            if (this._data[i].lineNo === no) {
                return this._data[i].lineScore;
            }
        }
    }

    private setLightFrame(index: number, frameIndex: number) {
        const right = find(this._rewardLineNo[index].toString(), SlotUnicorn.UIManager.instance.rightLightContent);
        right.getComponent(Sprite).spriteFrame = SlotUnicorn.UIManager.instance.lightFrames[frameIndex];
        const left = find(this._rewardLineNo[index].toString(), SlotUnicorn.UIManager.instance.leftLightContent);
        left.getComponent(Sprite).spriteFrame = SlotUnicorn.UIManager.instance.lightFrames[frameIndex];
    }

    public setMask() {
        this.turbleContent.getComponent(Mask).enabled = false;
    }

    public isAllLight() {
        for (let j = 0; j < this.turbleContent.children.length; j++) {
            const content = this.turbleContent.children[j];
            for (let k = 0; k < content.children.length; k++) {
                if (!content.children[k].activeInHierarchy) {
                    return false;
                }
            }
        }

        return true;
    }

    public hide() {
        this.blackNode.active = false;
        this.winTip.active = false;
        this._data = [];
        this.turbleContent.getComponent(Mask).enabled = true;
        this.unscheduleAllCallbacks();
        for (let i = 0; i < this.lineContent.children.length; i++) {
            this.lineContent.children[i].active = false;
            this._leftLightContent.children[i].getComponent(Sprite).spriteFrame = this.lightFrames[0];
            this._rightLightContent.children[i].getComponent(Sprite).spriteFrame = this.lightFrames[0];
        }

        for (let i = 1; i <= 5; i++) {
            this._rewardLines[i] = [];
        }
        for (let i = 0; i < 3; i++) {
            this._isElementLight[i] = [false, false, false];
            this.lightIndexList[i] = [];
        }
        this._rewardLineNo = [];

        for (let j = 0; j < this.turbleContent.children.length; j++) {
            const content = this.turbleContent.children[j];
            for (let k = 0; k < content.children.length; k++) {
                content.children[k].position = new Vec3(0, 638);
                content.children[k].active = false;
            }
        }
    }
}


