import { Vec3 } from 'cc';
import { Widget } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UnicoreResultItem } from './UnicoreResultItem';
import { SlotUnicorn } from '../UIManager';
import { Label } from 'cc';
import { Tween } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('UnicoreResult')
export class UnicoreResult extends Component {
    @property(UnicoreResultItem)
    bitWin: UnicoreResultItem = null;
    @property(UnicoreResultItem)
    mageWin: UnicoreResultItem = null;
    @property(UnicoreResultItem)
    superWin: UnicoreResultItem = null;
    @property(Label)
    winLab: Label = null;

    private static _instance: UnicoreResult = null;
    public static get instance() {
        return this._instance;
    }

    private _winScore: number = 0;
    private _level = 20;
    private _betScore = 0;

    public isNext = false;
    public isBest = false;
    private _numTw: Tween<any> = null;
    private _bigId = -1;   //一级音效id
    private _mageId = -1;   //二级音效id
    private _superId = -1;   //三级音效id
    private _payoutId = -1;   //滚动数字音效id

    protected onLoad() {
        if (UnicoreResult._instance) {
            this.node.destroy();
            return;
        }

        UnicoreResult._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        this.node.active = false;
        this.node.on(Node.EventType.TOUCH_END, this.onBtnNext, this);
    }

    protected onDestroy(): void {
        UnicoreResult._instance = null;
    }

    public show(betScore: number, winScore: number) {
        const mult = winScore / betScore;
        this._level = 20;
        this.isNext = false;
        this.isBest = false;
        this._winScore = winScore ? winScore : 0;
        this._betScore = betScore;
        if (!mult || mult < 10) return;
        AudioMgr.inst.play('audio/bgm_wins', 1, ModuleDef.SLOTS_UNICORN);
        AudioMgr.inst.playOneShot('audio/sfx_wins_payout', 1, ModuleDef.SLOTS_UNICORN).then(aeid => this._payoutId = aeid);
        this.node.active = true;
        if (mult >= 10) {
            this.winLab.string = "0.00";
            AudioMgr.inst.playOneShot('audio/sfx_wins_hit_big', 1, ModuleDef.SLOTS_UNICORN).then(aeid => this._bigId = aeid);
            this.bitWin.show(betScore, winScore, 0);
        }
    }

    public showNext(level: number, betScore: number, winScore: number) {
        this.isNext = false;
        if (level === 20) {
            this._level = 100;
            this.bitWin.hide();
            AudioMgr.inst.playOneShot('audio/sfx_wins_hit_mega', 1, ModuleDef.SLOTS_UNICORN).then(aeid => this._mageId = aeid);
            this.mageWin.show2(betScore, winScore, 20 * betScore);
        } else if (level === 100) {
            this._level = -1;
            this.mageWin.hide();
            AudioMgr.inst.playOneShot('audio/sfx_wins_hit_mega', 1, ModuleDef.SLOTS_UNICORN).then(aeid => this._superId = aeid);
            this.superWin.show2(betScore, winScore, 100 * betScore);
        }
    }

    public onBtnNext() {
        let maxBet = this._betScore * this._level;
        maxBet = this._winScore >= maxBet ? maxBet : this._winScore;
        if (this._level === -1) maxBet = this._winScore;
        if (this.isNext) {
            this.unschedule(this.toNext);
            this.toNext();
            if (this.isBest) {
                this.hide();
            }
        } else {
            if (this._level === 20) {
                if (this._bigId != -1) (AudioMgr.inst.stopOneShot(this._bigId, 'audio/sfx_wins_hit_big', ModuleDef.SLOTS_UNICORN), this._bigId = -1);
                this.bitWin.unAllCallbacks();
            } else if (this._level === 100) {
                if (this._mageId != -1) (AudioMgr.inst.stopOneShot(this._mageId, 'audio/sfx_wins_hit_mega', ModuleDef.SLOTS_UNICORN), this._mageId = -1);
                this.mageWin.unAllCallbacks();
            } else if (this._level === -1) {
                if (this._superId != -1) (AudioMgr.inst.stopOneShot(this._superId, 'audio/sfx_wins_hit_mega', ModuleDef.SLOTS_UNICORN), this._superId = -1);
                this.isBest = true;
                this.isNext = true;
                this.superWin.unAllCallbacks();
                return;
            }

            this.isNext = true;
            if (this._winScore > maxBet)
                this.scheduleOnce(this.toNext, 2);
            else this.isBest = true;
        }
    }

    private toNext() {
        this.showNext(this._level, this._betScore, this._winScore);
    }

    public hide() {
        this.onBtnClose();
        this.isNext = false;
        this.isBest = false;
        this.bitWin.hide();
        this.mageWin.hide();
        this.superWin.hide();
        SlotUnicorn.UIManager.instance.resetGame();
        SlotUnicorn.UIManager.instance.setScore(this._winScore);
    }

    stopShot(shotName: string) {
        switch (shotName) {
            case 'big':
                if (this._bigId != -1) (AudioMgr.inst.stopOneShot(this._bigId, 'audio/sfx_wins_hit_big', ModuleDef.SLOTS_UNICORN), this._bigId = -1);
                break;
            case 'mage':
                if (this._mageId != -1) (AudioMgr.inst.stopOneShot(this._mageId, 'audio/sfx_wins_hit_mega', ModuleDef.SLOTS_UNICORN), this._mageId = -1);
                break;
            case 'super':
                if (this._superId != -1) (AudioMgr.inst.stopOneShot(this._superId, 'audio/sfx_wins_hit_mega', ModuleDef.SLOTS_UNICORN), this._superId = -1);
                break;
            case 'payout':
                if (this._payoutId != -1) (AudioMgr.inst.stopOneShot(this._payoutId, 'audio/sfx_wins_payout', ModuleDef.SLOTS_UNICORN), this._payoutId = -1);
                break;
        }
    }

    private onBtnClose() {
        this.node.active = false;
    }
}


