import { ToggleContainer } from 'cc';
import { find, UITransform, tween, Vec3 } from 'cc';
import { Slider } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotUnicornData } from '../../data/GameConst';
import { UIOpacity } from 'cc';
import { SlotUnicorn } from '../UIManager';
import { Widget } from 'cc';
import { Toggle } from 'cc';
import * as SlotGameManager from '../GameManager';
const { ccclass, property } = _decorator;

let GameManager = null;
@ccclass('UnicoreAutoManager')
export class UnicoreAutoManager extends Component {
    @property(Node)
    content: Node = null;
    @property(Label)
    coinLab: Label = null;
    @property(Label)
    betLab: Label = null;
    @property(Label)
    winLab: Label = null;
    @property(ToggleContainer) toggleRound: ToggleContainer = null;
    @property(Slider) reduceSlider: Slider = null;
    @property(Node) reduceproBar: Node = null;
    @property(Label) reduceLabel: Label = null;
    @property(Slider) addSlider: Slider = null;
    @property(Node) addproBar: Node = null;
    @property(Label) addLabel: Label = null;
    @property(Slider) winSlider: Slider = null;
    @property(Node) winproBar: Node = null;
    @property(Label) winLabel: Label = null;
    private static _instance: UnicoreAutoManager = null;
    public static get instance() {
        return this._instance;
    }

    protected onLoad() {
        if (UnicoreAutoManager._instance) {
            this.node.destroy();
            return;
        }

        UnicoreAutoManager._instance = this;
        GameManager = SlotGameManager.SlotUnicorn.GameManager;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        this.node.active = false;
    }

    protected onDisable(): void {
        UnicoreAutoManager._instance = null;
    }

    showPanel() {
        this.node.active = true;
        this.reduceSlider.progress = 0;
        this.addSlider.progress = 0;
        this.winSlider.progress = 0;
        this.reduceproBar.getComponent(UITransform).width = 0;
        this.addproBar.getComponent(UITransform).width = 0;
        this.winproBar.getComponent(UITransform).width = 0;
        // this.reduceLabel.string = I18n.ins().t("none");
        // this.addLabel.string = I18n.ins().t("none");
        // this.winLabel.string = I18n.ins().t("none");
        this.coinLab.string = SlotUnicorn.UIManager.instance.coinLab.string;
        this.betLab.string = SlotUnicorn.UIManager.instance.betLab.string;
        this.winLab.string = SlotUnicorn.UIManager.instance.winLab.string;

        SlotUnicornData.GameConst.reduceLabel = 0
        SlotUnicornData.GameConst.addLabel = 0
        SlotUnicornData.GameConst.winLabel = 0
        this.node.scale = Vec3.ZERO;
        this.node.getComponent(UIOpacity).opacity = 0;
        tween(this.node).removeSelf();
        tween(this.node).to(.25, { scale: Vec3.ONE }).start();
        tween(this.node.getComponent(UIOpacity)).to(.25, { opacity: 255 }).start();
        this.AutoEnable()
    }

    protected AutoEnable(): void {
        for (let index = 0; index < this.toggleRound.node.children.length; index++) {
            let tog = this.toggleRound.node.children[index];
            if (index === 0) tog.getComponent(Toggle).isChecked = true;
            tog.name = index.toString();
            SlotUnicornData.GameConst.autoCountIndex = 0
            find("Background/Label", tog).getComponent(Label).string = SlotUnicornData.GameConst.Auto.AutoIndex[index].toString();
            find("Checkmark/Label", tog).getComponent(Label).string = SlotUnicornData.GameConst.Auto.AutoIndex[index].toString();
        }

    }

    onSelectRound(data) {
        // SlotUnicornData.GameConst.autoCountIndex = tool.convert.toNumber(data.node.name);
    }

    onBtnConfirm() {
        this.onBtnClose()
        // emitter.emit(SlotUnicornData.GameConst.eventName.startAutoGame);
        SlotUnicorn.UIManager.instance.startAuto();
    }

    reduce_Label(lab) {
        const progress = this.reduceSlider.progress;

        const progressS = (Math.floor(progress * 5) + 5).toFixed(0);
        if (progressS == "0") {
            this.reduceLabel.string = "5"
        } else {
            this.reduceLabel.string = progressS
        }
        // SlotUnicornData.GameConst.reduceLabel = tool.convert.toNumber(this.reduceLabel.string)
    }

    add_labbel(lab) {
        const progress = this.addSlider.progress;

        const progressS = (Math.floor(progress * 4900) + 100).toFixed(0);
        if (progressS == "0") {
            this.addLabel.string = "100"
        } else {
            this.addLabel.string = progressS
        }
        // SlotUnicornData.GameConst.addLabel = tool.convert.toNumber(this.addLabel.string)
    }

    win_label(lab) {
        const progress = this.winSlider.progress;

        const progressS = (Math.floor(progress * 490) + 10).toFixed(0);
        if (progressS == "0") {
            this.winLabel.string = "10"
        } else {
            this.winLabel.string = progressS
        }
        // SlotUnicornData.GameConst.winLabel = tool.convert.toNumber(this.winLabel.string)
    }

    onReduceSlider() {
        const reduceSlider = this.reduceSlider.progress;
        this.reduceproBar.getComponent(UITransform).width = reduceSlider * 1000;
        // let reduceLabel = tool.convert.toNumber((reduceSlider * 10).toFixed(0))
        // this.reduce_Label(reduceLabel);
    }

    onAddSlider() {
        const addSlider = this.addSlider.progress;
        this.addproBar.getComponent(UITransform).width = addSlider * 1000;
        // let addLabel = tool.convert.toNumber((addSlider * 5000).toFixed(0))
        // this.add_labbel(addLabel);
    }

    onWinSlider() {
        const winSlider = this.winSlider.progress;
        this.winproBar.getComponent(UITransform).width = winSlider * 1000;
        // let winLabel = tool.convert.toNumber((winSlider * 500).toFixed(0))
        // this.win_label(winLabel);
    }

    public onBtnClose() {
        // tween(this.content).to(0.25, { position: new Vec3(0, -1800) }).call(() => {
        //     this.node.active = false;
        // }).start();

        tween(this.node).removeSelf();
        tween(this.node).to(.25, { scale: Vec3.ZERO }).call(() => {
            this.node.active = false;
        }).start();
        tween(this.node.getComponent(UIOpacity)).removeSelf();
        tween(this.node.getComponent(UIOpacity)).to(.25, { opacity: 0 }).start();
    }
}


