import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UnicornUtils } from '../../data/UnicornUtils';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { UnicoreResult } from './UnicoreResult';
import { SlotUnicorn } from '../UIManager';
import { find } from 'cc';
import { sp } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('UnicoreResultItem')
export class UnicoreResultItem extends Component {
    @property(Node)
    content: Node = null;
    @property(Label)
    winLab: Label = null;
    @property(Node)
    effectNode: Node = null;
    @property(Node)
    lightEffect1: Node = null;
    @property(Node)
    lightEffect2: Node = null;
    @property
    panelLevel: number = -1; //-1最大的页面等级

    private _tw = null;
    private _betScore = 0;
    private _winScore = 0;

    public show(betScore: number, winScore: number, startBet: number) {
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        this.content.scale = new Vec3(1, 0, 1);
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            level = 100;
            maxBet = winScore;
        }
        this.winLab.string = Num.exchangeToThousands(1, startBet);
        tween(this.content).to(0.5, { scale: Vec3.ONE })
            .call(() => {
                find('unicorn', this.content)?.getComponent(sp.Skeleton)?.setAnimation(0, '1', true);
                if (this.panelLevel === 20) {
                    startBet = 0;
                    maxBet = betScore * this.panelLevel;
                    maxBet = winScore >= maxBet ? maxBet : winScore;
                } else if (this.panelLevel === 100) {
                    startBet = betScore * 20;
                    maxBet = betScore * this.panelLevel;
                    maxBet = winScore >= maxBet ? maxBet : winScore;
                } else {
                    startBet = betScore * 100;
                    maxBet = winScore;
                }
                const cb = () => {
                    if (winScore > maxBet) {
                        UnicoreResult.instance.showNext(this.panelLevel, betScore, winScore);
                    } else {
                        this.scheduleOnce(() => {
                            UnicoreResult.instance.hide();
                        }, 2);

                        UnicoreResult.instance.isNext = true;
                        UnicoreResult.instance.isBest = true;
                    }
                }
                this._tw = UnicornUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.6, callback: cb.bind(this) });
                this._tw.start();
            }).start();
    }

    public show2(betScore: number, winScore: number, startBet: number) {
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        this.content.scale = new Vec3(1, 0, 1);
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            level = 100;
            maxBet = winScore;
        }

        this.winLab.string = Num.exchangeToThousands(1, startBet);
        if (this.panelLevel === 20) {
            startBet = 0;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            startBet = betScore * 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else {
            startBet = betScore * 100;
            maxBet = winScore;
        }
        const cb = () => {
            if (winScore > maxBet) {
                UnicoreResult.instance.showNext(this.panelLevel, betScore, winScore);
            } else {
                this.scheduleOnce(() => {
                    UnicoreResult.instance.hide();
                }, 2);
                UnicoreResult.instance.stopShot('payout');
                UnicoreResult.instance.isNext = true;
                UnicoreResult.instance.isBest = true;
            }
        }
        this._tw = UnicornUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.1, callback: cb.bind(this) });
        this._tw.start();
        tween(this.content).to(0.5, { scale: Vec3.ONE })
            .call(() => {

            }).start();
    }

    public unAllCallbacks() {
        this.unscheduleAllCallbacks();
        let maxBet = this._betScore * this.panelLevel;
        maxBet = this._winScore >= maxBet ? maxBet : this._winScore;
        if (this.panelLevel === -1) maxBet = this._winScore;
        this.winLab.string = Num.exchangeToThousands(1, maxBet);
        if (this._winScore <= maxBet) {
            UnicoreResult.instance.stopShot('payout');
            this.scheduleOnce(() => {
                UnicoreResult.instance.hide();
            }, 2);
        }
        this._tw && (this._tw.stop(), this._tw = null);
    }

    protected onDisable(): void {
        this.unscheduleAllCallbacks();
        // if (this.panelLevel === 20) {
        //     find('unicorn', this.content).getComponent(sp.Skeleton).setAnimation(0, null);
        // }
        UnicoreResult.instance.stopShot('big');
        UnicoreResult.instance.stopShot('mage');
        this._tw && (this._tw.stop(), this._tw = null);
        this.hide();
        this.lightEffect1.active = false;
        this.lightEffect2.active = false;
    }

    public hide() {
        this.node.active = false;
    }
}


