import { _decorator, Component, Node } from 'cc';
import { SlotUnicorn } from './GameManager';
import { SlotUnicornData } from '../data/GameConst';
import { UnicoreDetailsManager } from './UnicoreDetailsManager';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { sp } from 'cc';
import { Vec3 } from 'cc';
import * as UI from './UIManager';
import { find } from 'cc';
import { UnicornAniManager } from './UI/UnicornAniManager';
import { tween } from 'cc';
import { easing } from 'cc';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { UnicornUtils } from '../data/UnicornUtils';
const { ccclass, property } = _decorator;

const skePos = {
    101: new Vec3(-7, -24),
    102: new Vec3(-3, -68),
    103: new Vec3(),
    104: new Vec3(3, -20),
    106: new Vec3(-1, -18),
    105: new Vec3(-6, -12),
    201: new Vec3(0, 0),
}

@ccclass('UnicornGameItem')
export class UnicornGameItem extends Component {
    @property(Sprite)
    icon: Sprite = null;
    @property(sp.Skeleton)
    ske: sp.Skeleton = null;
    @property(Node)
    effectNode: Node = null;
    @property(Node)
    wEffectNode: Node = null;
    @property(Node)
    effectFrontNode: Node = null;

    private _faceId: number = 0;
    private _lastPath: string = '';

    protected onLoad(): void {
        this.ske.setCompleteListener((result) => {
            if (result.animation.name === "2") {
                this.setAnimation(this._faceId, "1", true);
            }
        });

        UnicornUtils.instance.setNodesByComponent(this.node.parent);
    }

    public initGameItem(faceId: number) {
        this._faceId = faceId;
        this.setElement(faceId);
        this.node.on(Node.EventType.TOUCH_END, this.touchEnd, this);
    }

    public resetItem() {
        this.wEffectNode.active = false;
    }

    public setNull() {
        this.ske.skeletonData = null;
        this.icon.spriteFrame = null;
        this.node.off(Node.EventType.TOUCH_END, this.touchEnd, this);
    }
    private skes = {};
    private skins = {};
    /**
     * 设置元素的展示
     * @param faceId - ID
     */
    public setElement(faceId: number) {
        if (faceId === 201) {
            const path = `spine/${faceId}/skeleton`;
            this.wEffectNode.active = true;
            this.ske.node.scale = new Vec3(0.9, 0.9);

            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_UNICORN, path, sp.SkeletonData, (err: Error, skeData: sp.SkeletonData) => {
                if (err || !this.ske || !this.ske.node) return console.error("--换面--", err);
                // this.skes[path] = skeData;

                this.ske.node.active = true;
                // this.ske.node.position = skePos[faceId];
                this.ske.skeletonData = skeData;
                this.ske.setAnimation(0, '1', true);
            });
        } else {
            const path = `textures/turntable/element/${faceId}`;
            this.ske.node.scale = Vec3.ONE;
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_UNICORN, path, SpriteFrame, (err: Error, spriteFrame: SpriteFrame) => {
                if (err || !this.icon || !this.icon.node) return console.error("--换面--", path);
                this.icon.node.active = true;
                this.icon.spriteFrame = spriteFrame;
            });
        }
    }

    public addGift() {
        if (this._faceId === 201) {
            tween(this.node).to(0.2, { scale: new Vec3(1.1, 1.1) }, { easing: easing.backIn }).call(() => {
                tween(this.node).to(0.2, { scale: Vec3.ONE }, { easing: easing.elasticOut }).start();
            }).start();
            UI.SlotUnicorn.UIManager.instance.addGiftEffect(this.node.worldPosition.clone());
        }
    }

    /**
     * 关闭除指定 ID 外的所有特效节点
     * @param faceId - 要保留显示特效的节点 ID，默认为 0
     */
    public closeEffect(faceId = 0) {
        for (let i = 0; i < this.effectFrontNode.children.length; i++) {
            if (this.effectFrontNode.children[i].name === faceId.toString()) continue;
            this.effectFrontNode.children[i].active = false;
        }
        this.wEffectNode.active = false;
        for (let i = 0; i < this.effectNode.children.length; i++) {
            if (this.effectNode.children[i].name === faceId.toString()) continue;
            this.effectNode.children[i].active = false;
        }
    }

    /**
     * 显示指定 ID 的特效节点
     */
    public showEffect() {
        this.closeEffect(this._faceId);
        if (this._faceId === 201) {
            this.wEffectNode.active = true;
        } else {
            const effect = find(this._faceId.toString(), this.effectNode);
            effect.active = true;
        }

        this.wEffectNode.active = false;
        find(this._faceId.toString(), this.effectFrontNode).active = true;
    }

    /**
     * 设置指定 ID 的动画
     * @param faceId - ID
     * @param aniName - 动画名称
     * @param isLoop - 是否循环播放动画，默认为 false
     */
    public setAnimation(faceId: number, aniName: string, isLoop: boolean = false) {
        this._faceId = faceId;
        let path = `spine/${this._faceId}/skeleton`;
        this.ske.node.scale = Vec3.ONE;
        if (this._faceId === 201) {
            this.ske.node.scale = new Vec3(0.9, 0.9);
            if (aniName === "2") {
                path = `spine/${this._faceId}_click/skeleton`;
            }
        }

        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_UNICORN, path, sp.SkeletonData, (err: Error, skeData: sp.SkeletonData) => {
            if (err || !this.ske || !this.ske.node) return console.error("--换面--", err);
            this.ske.node.active = true;
            // this.ske.node.position = skePos[faceId];
            this.ske.skeletonData = skeData;
            this.ske.setAnimation(0, aniName, isLoop);
        });
    }

    private touchEnd() {
        if (SlotUnicorn.GameManager.instance.gameStatus === SlotUnicornData.GameStatus.start) return;
        UnicornAniManager.instance.resetDowntime(); //重置倒计时
        UnicoreDetailsManager.instance.showDetails(this._faceId, this.node.position, this.node.parent.name); //显示元素详情
    }

    protected onDisable(): void {
        this.node.off(Node.EventType.TOUCH_END, this.touchEnd, this);
    }
}


