import { UITransform } from 'cc';
import { Tween } from 'cc';
import { macro } from 'cc';
import { SpriteFrame } from 'cc';
import { RichText } from 'cc';
import { Sprite } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotUnicorn } from './UIManager';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

//<img src='101'/>
const NoticeMsg = ['broadcast1', 'broadcast2', 'broadcast3', "broadcast4"];

@ccclass('UnicornNoticeView')
export class UnicornNoticeView extends Component {
    @property(Sprite)
    noticeBg: Sprite = null;
    @property(RichText)
    msgLab: RichText = null;
    @property(Node)
    scoreNode: Node = null;
    @property(Sprite)
    winSpr: Sprite = null;
    @property(Label)
    winScoreLab: Label = null;
    @property([SpriteFrame])
    winFrames: SpriteFrame[] = [];
    @property([SpriteFrame])
    winTextFrames: SpriteFrame[] = [];
    @property(Node)
    effectBg: Node = null;
    @property(Node)
    effectBg2: Node = null;

    private _noticeIndex = 2;
    private _msgTween: Tween<Node> = null;
    private _winScore = 0;
    private _isShow = false;

    //==============结算分数===================
    public balanceScore(betScore: number, winScore: number, isMini: boolean, isAllSame: number) {
        if (this._isShow) return;
        const mult = winScore / betScore;
        this._winScore = winScore;
        if (!mult) {
            SlotUnicorn.UIManager.instance.resetGame();
            return;
        }
        this._isShow = true;
        if (mult > 0 && mult < 10) {
            this.noticeBg.spriteFrame = this.winFrames[0];
            this.effectBg2.active = true;
        } else if (mult >= 10 && mult < 20) {
            this.noticeBg.spriteFrame = this.winFrames[1];
            this.effectBg.active = true;
        } else if (mult >= 100) {
            this.noticeBg.spriteFrame = this.winFrames[3];
            this.effectBg.active = true;
        }

        if (isAllSame) {
            if (isMini) {
                SlotUnicorn.UIManager.instance.setWinMult(mult);
                this.winScoreLab.string = Num.exchangeToThousands(1, winScore / 10);
            } else {
                SlotUnicorn.UIManager.instance.showReward(mult, winScore);
                this.winScoreLab.string = Num.exchangeToThousands(1, winScore);
            }
        } else {
            SlotUnicorn.UIManager.instance.showReward(mult, winScore);
            this.winScoreLab.string = Num.exchangeToThousands(1, winScore);
        }
        this.msgLab.node.active = false;
        this.scoreNode.active = true;

        AudioMgr.inst.playOneShot("audio/sfx_win_line", 1, ModuleDef.SLOTS_UNICORN);
        tween(this.node).removeSelf();
        tween(this.node).to(.2, { scale: new Vec3(1.2, 1.2, 1.2) })
            .delay(.1)
            .to(.2, { scale: Vec3.ONE })
            .call(() => {
                if (mult < 10) {
                    if (!isMini) SlotUnicorn.UIManager.instance.gameOver();
                    else if (isMini && !isAllSame) {
                        SlotUnicorn.UIManager.instance.resetGame();
                    }
                    SlotUnicorn.UIManager.instance.setScore(winScore);
                }
            })
            .union()
            .start();
    }

    public updateTip() {
        this.winScoreLab.string = Num.exchangeToThousands(1, this._winScore);
    }

    public hideScore() {
        this.effectBg.active = false;
        this.effectBg2.active = false;
        this.scoreNode.active = false;
        this._isShow = false;
        this.msgLab.node.active = true;
        this.noticeBg.spriteFrame = this.winFrames[0];
        this.winSpr.spriteFrame = this.winTextFrames[0];
    }

    //================轮播===================
    public showNotice() {
        this.schedule(this.setNotice, 7, macro.REPEAT_FOREVER, .1);
    }

    private setNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
        this.msgLab.string = LanguageData.getLangByID(NoticeMsg[this._noticeIndex]);
        this.scheduleOnce(() => {
            const size = this.msgLab.getComponent(UITransform).contentSize;
            if (size.width > 860) {
                this.msgLab.node.position = new Vec3((size.width - 860) / 2);
                this.scheduleOnce(() => {
                    tween(this.msgLab.node).removeSelf();
                    this._msgTween = tween(this.msgLab.node).to(5, { position: new Vec3(-size.width, 0) });
                    this._msgTween.start();
                }, 1);

            }
            else this.msgLab.node.position = Vec3.ZERO;
        }, 1 / 60);
        this._noticeIndex++;
        if (this._noticeIndex >= NoticeMsg.length) this._noticeIndex = 0;
    }

    public stopNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
    }
}


