import { _decorator, Component, Node } from 'cc';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { SceneDef } from 'db://assets/scripts/SceneDef';
const { ccclass, property } = _decorator;

@ccclass('SlotsUnicornGameHUD')
export class SlotsUnicornGameHUD extends Component {
    start() {
        if (!UserMgr.inst.uid) {
            tgx.SceneUtil.loadScene(SceneDef.START);
            return;
        }
        tgx.UIMgr.inst.closeAll();
    }


    update(deltaTime: number) {

    }

    async onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                await RoomMgr.inst.exitRoom();
            }
        });
    }
}


