import { ccenum } from 'cc';
import { Sprite } from 'cc';
import { Vec3 } from 'cc';
import { SpriteFrame } from 'cc';
import { Toggle } from 'cc';
import { color } from 'cc';
import { tween } from 'cc';
import { CCInteger } from 'cc';
import { Button } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

enum EType {
    DARK,
    GRAY
}

ccenum(EType);

@ccclass('UnicornBaseButton')
export class UnicornBaseButton extends Button {
    @property
    isScale: boolean = true;
    @property({ type: EType, displayName: "禁用类型" })
    interactableType = EType.DARK;
    @property(CCInteger)
    dark = 125;
    @property({ displayName: '是否用作复选按钮', tooltip: '选中状态图片放0  未选中图片放1' })
    isToggle: boolean = false;

    private _isChecked: boolean = true;
    @property({ displayName: '是否选中', tooltip: '复选按钮的选中状态', visible: function () { return this.isToggle; } })
    get isChecked() { return this._isChecked }
    set isChecked(v: boolean) {
        this._isChecked = v
        this.node.getComponent(Sprite).spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
    }
    @property({ type: [SpriteFrame], tooltip: '复选按钮要切换的图片', visible: function () { return this.isToggle; } })
    toggleFrames: SpriteFrame[] = [];

    private _st: Sprite;
    protected onLoad(): void {
        this._st = this.node.getComponent(Sprite);
        this.node.on(Button.EventType.CLICK, () => {
            if (!this.interactable) return;
            // root.audio.play('Click');
            if (this.isToggle) {
                this.isChecked = !this.isChecked;
                if (!this._st) {
                    this._st = this.node.getComponent(Sprite);
                }
                this._st.spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
            }
            if (this.transition == 0 && this.isScale) {
                tween(this.node).to(.1, { scale: new Vec3(1.05, 1.05, 1.05) }).to(.1, { scale: Vec3.ONE }).start();
            }
        });
    }

    public get interactable() {
        return this._interactable;
    }
    public set interactable(v: boolean) {
        if(!this._st) this._st = this.node.getComponent(Sprite);
        if (this._st) {
            if (this.interactableType == EType.DARK) {
                const c = v ? 255 : this.dark;
                this._st.color = color(c, c, c, 255);
            }
            if (this.interactableType == EType.GRAY) {
                this._st.grayscale = !v;
            }
        }
        this._interactable = v;
    }
}


