import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotTigerElement, SlotUnicornData } from '../data/GameConst';
import * as SlotUnicornUI from './UIManager';
import { randomRangeInt } from 'cc';
import { SpriteFrame, Sprite } from 'cc';
import { UnicornGameItem } from './UnicornGameItem';
import { Prefab } from 'cc';
import { EventTouch } from 'cc';
import { input } from 'cc';
import { Input } from 'cc';
import { UnicornAniManager } from './UI/UnicornAniManager';
import { UnicornPoolManager } from '../base/UnicornPoolManager';
import { UITransform } from 'cc';
import { view } from 'cc';
import { Vec3 } from 'cc';
const { ccclass, property } = _decorator;

export namespace SlotUnicorn {
    //五条线的下标
    const AllLine = {
        1: [1, 1, 1],
        2: [0, 0, 0],
        3: [2, 2, 2],
        4: [0, 1, 2],
        5: [2, 1, 0]
    };

    /**
     * 游戏管理类
     */
    @ccclass('GameManager')
    export class GameManager extends Component {
        @property(Prefab)
        giftPrefab: Prefab = null;
        @property(Prefab)
        elementPrefab: Prefab = null;
        @property({ type: UITransform, tooltip: "游戏的主节点" })
        gameMainTran: UITransform = null;

        private static _instance: GameManager = null;
        public static get instance() {
            return this._instance;
        }

        public betScore: number[] = [];
        public elementOdd: number[] = []; //不除100
        public lineCount: number = 0;
        public speed: number = 1;
        public gameStatus: SlotUnicornData.GameStatus = SlotUnicornData.GameStatus.over;
        public miniElementId: number = 0;
        public isSpeed: boolean = false;

        onLoad() {
            if (GameManager._instance) {
                this.node.destroy();
                return;
            }

            GameManager._instance = this;
            this.gameSetup();
        }

        protected onDestroy(): void {
            GameManager._instance = null;
        }

        private gameSetup() {
            //适配
            // const v = view.getVisibleSize();
            // const scaleV = v.width / this.gameMainTran.height;
            // this.gameMainTran.node.scale = new Vec3(scaleV, scaleV, 1);
            // this.events.put(SlotUnicornData.GameConst.MessgeDefine.Response.GameScene + store.room.id, this.initGameScene.bind(this));

            input.on(Input.EventType.MOUSE_DOWN, this.touchStart, this);
            UnicornPoolManager.instance.createPool({
                prefab: this.elementPrefab,
                name: UnicornPoolManager.PoolsName.element,
                initCount: 20
            });

            UnicornPoolManager.instance.createPool({
                prefab: this.giftPrefab,
                name: UnicornPoolManager.PoolsName.giftEffect,
                initCount: 20
            });
        }

        public startGame() {
            this.initGameScene({
                betScore: [100, 200, 300, 400, 500, 600, 700, 800, 900], elementOdd: { 101: 3, 102: 5, 103: 8, 104: 10, 105: 25, 106: 100, 201: 250 }, lineCount: 10, fakeMiniRate: 10,
                pointScores: [100, 300, 500, 700, 900, 1100], mainWheel: [{ lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }],
                miniWheel: [{ lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }]
            })
        }

        private touchStart() {
            // event.preventSwallow = true;
            UnicornAniManager.instance.resetDowntime();
        }

        initGameScene(res) {
            console.log('-----', res);
            this.betScore = res.betScore;
            this.elementOdd = res.elementOdd;
            this.lineCount = res.lineCount;
            SlotUnicornUI.SlotUnicorn.UIManager.instance.initUI();
        }

        reconnect() {
            SlotUnicornUI.SlotUnicorn.UIManager.instance.reconnect();
        }

        /**
         * 判断是否连成线，并且返回所有已连线编号。
         * 如果有已知连成线的编号则忽略该线
         * @param haveElement 竖向，元素编号位置是否已有
         * @param knownNo 已知已经连成线的编号
         */
        public getAllLineNo(haveElement: boolean[][], knownNo: number[] = []) {
            const lineNo: number[] = [];

            for (const key in AllLine) {
                if (Object.prototype.hasOwnProperty.call(AllLine, key)) {
                    // if (knownNo.includes(Number(key))) continue;
                    const line = AllLine[key];
                    let num = 0;
                    for (let i = 0; i < line.length; i++) {
                        //i line中的第几列，line[i]第i列的行
                        //第i列的第n个元素是否已掉落
                        if (!haveElement[i][line[i]]) break;
                        num++;
                    }
                    if (num === 3) lineNo.push(Number(key));
                }
            }

            return lineNo;
        }

        /**
         * 获取连线对应的中奖下标
         * @param lineNo 
         * @returns 
         */
        public getLineNo(lineNo: string) {
            return AllLine[lineNo];
        }

        /**
     * 把0的填充到已点亮下标
     */
        public zeroToList(res: SlotTigerElement[]) {
            const indexList = [];
            for (let k = 0; k < 3; k++) {
                indexList[k] = [];
            }
            for (let i = 0; i < res.length; i++) {
                for (let j = 0; j < res[i].elementIds.length; j++) {
                    if (res[i].elementIds[j] === 0) {
                        indexList[i].push(j);
                    }
                }
            }

            // console.log(indexList);
            return indexList;
        }

        /**
     * 换牌面
     * @param node 带有精灵的节点
     * @param random 是否随机-1随机
     */
        public setImage(node: Node, random = -1) {
            if (random === 0) {
                node.getComponent(UnicornGameItem).setNull();
                return;
            }
            let id = random;
            //随机取牌面
            if (random === -1) {
                id = randomRangeInt(101, 107);
                if (id > 106) id = 201;
            }
            const gameItem = node.getComponent(UnicornGameItem);
            gameItem.setNull();
            gameItem.initGameItem(id);
            gameItem.resetItem();
            // const path = `textures/turntable/${id}/spriteFrame`;
            // const path = `textures/turntable/element/${id}`;
            // root.game.bundle.load(path, (err: Error, spriteFrame: SpriteFrame) => {
            //     if (err) return console.error("--换面--", err);
            //     node.getComponent(Sprite).spriteFrame = spriteFrame;
            //     node.getComponent(UnicornGameItem).initGameItem(id);
            // });
        }

        public getElementIds() {
            const elements = [];
            for (let i = 0; i < 3; i++) {
                const item = { elementIds: [] }
                for (let j = 0; j < 3; j++) {
                    const id = randomRangeInt(101, 107);
                    item.elementIds.push(id);
                }
                elements.push(item);
            }

            return elements;
        }
    }
}


