import { Enum } from 'cc';
import { macro } from 'cc';
import { sp } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotUnicorn } from '../GameManager';
import { SlotUnicornData } from '../../data/GameConst';
import { Vec3 } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

enum UnicornAni {
    common, //普通无动作
    stand,
    amazed,
    jump,
    happy,
    shake,
    leer,
    anger,
    gaze,
    yawn,
    fete
}
Enum(UnicornAni);

enum UnicornState {
    other, //其他状态
    yawn, //打哈切状态
}
Enum(UnicornState);

const yawn_intervalTime = 30; //打哈欠间隔时间
const stand_intervalTime = 5; //待机间隔时间
const gaze_intervalTime = 18; //注视间隔时间
/**
 * 独角兽动作管理
 */
@ccclass('UnicornAniManager')
export class UnicornAniManager extends Component {
    @property(sp.Skeleton)
    unicorn: sp.Skeleton = null;

    private static _instance: UnicornAniManager = null;
    public static get instance() {
        return this._instance;
    }

    private _unicornAni: UnicornAni = UnicornAni.common;
    private _unicornState: UnicornState = UnicornState.other;
    private _isPlayLoop: boolean = false;
    private _lastPath: string = '';

    protected onLoad() {
        if (UnicornAniManager._instance) {
            this.node.destroy();
            return;
        }
        UnicornAniManager._instance = this;
        //开启各类动作计时器
        this.playStand();
        this.schedule(this.playGaze, gaze_intervalTime, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.playYawn, yawn_intervalTime);

        this.unicorn.setCompleteListener((ani) => {
            if (this._isPlayLoop) return;
            if (this._unicornAni === UnicornAni.yawn) {
                this.scheduleOnce(this.playYawn, yawn_intervalTime);
            }
            this._unicornAni = UnicornAni.common;
            this.playStand();
        });
    }

    protected onDestroy(): void {
        UnicornAniManager._instance = null;
    }

    private skes = {};
    //加载独角兽动作
    private loadUnicornAni(aniName: string, isLoop: boolean) {
        //只有没在播放其他动作时才播放当前动作
        if (this._unicornAni != UnicornAni.common && this._unicornAni != UnicornAni.stand) return;
        this._isPlayLoop = isLoop;
        const path = `spine/Unicorn@${aniName}/skeleton`;
        if (aniName === 'fete') this.unicorn.node.position = new Vec3(0, 1015);
        else this.unicorn.node.position = new Vec3(70, 1015);
        this._unicornAni = UnicornAni[aniName];

        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_UNICORN, path, sp.SkeletonData, (err: Error, fab: sp.SkeletonData) => {
            if (err) return console.error("--独角兽动作--", err);
            this.unicorn.skeletonData = fab;
            this.unicorn.setAnimation(0, 'animation', isLoop);
        });
    }

    //重启打哈切计时器
    public resetDowntime() {
        this.unschedule(this.playYawn);
        this.scheduleOnce(this.playYawn, yawn_intervalTime);
        //打断了打哈欠状态
        if (this._unicornState === UnicornState.yawn) {
            this._unicornState = UnicornState.other;
            this.schedule(this.playGaze, gaze_intervalTime, macro.REPEAT_FOREVER);
        }
    }

    //播放待机动作
    public playStand() {
        this.loadUnicornAni('stand', true);
    }

    //播放惊讶动作
    public playAmazed() {
        this.loadUnicornAni('amazed', false);
    }

    //播放跳动作
    public playJump() {
        this._unicornAni = UnicornAni.common;
        this.loadUnicornAni('jump', true);
    }

    //播放庆祝动作
    public playFete() {
        this._unicornAni = UnicornAni.common;
        this.loadUnicornAni('fete', true);
    }

    //播放开心动作
    public playHappy() {
        this._unicornAni = UnicornAni.common;
        this.loadUnicornAni('happy', false);
    }

    //播放开心摇头动作
    public playShake() {
        //强行换动作
        this._unicornAni = UnicornAni.common;
        this.loadUnicornAni('shake', true);
    }

    //播放抛媚眼动作
    public playLeer() {
        this.loadUnicornAni('leer', false);
    }

    //播放生气动作
    public playAngry() {
        //文件夹单词错误
        this.loadUnicornAni('anger', false);
    }

    //播放注视动作
    public playGaze() {
        if (SlotUnicorn.GameManager.instance.gameStatus === SlotUnicornData.GameStatus.start) return;
        this.loadUnicornAni('gaze', false);
    }

    //播放打哈欠动作
    public playYawn() {
        if (SlotUnicorn.GameManager.instance.gameStatus === SlotUnicornData.GameStatus.start) return;
        this.unscheduleAllCallbacks();
        this._unicornState = UnicornState.yawn;
        this.loadUnicornAni('yawn', false);
    }
}


